import React, { Component } from 'react'
import axios from "../../../server/node_modules/axios";
import { Redirect } from 'react-router-dom';




// let myKey = "d759a614b16c0c1c0295b4313e94aeec";
class Cart extends Component {

    state = { isError: false, RedirectToCart: false, MyCart: [] };

    cart = () => {

        axios.post('/cart/id', {
            MyCart: this.state.MyCart
        }).then(res => {

            if (res.status === 201) {
                this.setState({ RedirectToCart: true });
                this.props.setUser({ MyCart: this.state.MyCart });
            }
            else {
                this.setState({ isError: true })
                console.log(`error code : ${res.status}`);

            }

        }).catch(err => {
            this.setState({ isError: true })
            console.log(err);
        })
    }
    
    

    // GetUserItem = () => {
    //     axios.get(`https://api.themoviedb.org/3/movie/530915/external_ids?api_key=${this.props.myKey}`)
    //         .then(res => {
    //             console.log(res.data.movie);

    //         }).catch(err => {

    //             console.log(err);
    //         });
    // }







    render() {
        if (this.state.RedirectToCart) {
            return <Redirect to='/cart' />
        }
        return (
            <div>
                <h1>Cart page</h1>
                <br></br>
                <br></br>
                <ul> User List
                      <li>{this.props.movie.title}</li>
                </ul>

            </div>
        )
    }
}
export default Cart;