import React, { Component } from "react";
import axios from "../../../server/node_modules/axios";
import SearchArea from "./mySearchArea";
import MovieList from "./movie_list_Results";
import "./home.css";
import { Card, Row, Col, Container } from "react-bootstrap";
import { Redirect } from "react-router-dom";


const baseImgUrl = "https://image.tmdb.org/t/p/w500";
let myKey = "d759a614b16c0c1c0295b4313e94aeec";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      moviesTop: [], // home-movies
      movies: [], // home-search-movies
      isLoaded: false,
      moviesPopular: [],
      tvShowsTop: [],
      searchTerm: "",
      tvShowsPopular: [],
      totalResuolts: 0,
      currentPage: 1,
      movie: null,
      flag: false,
      userList : []
    };
    console.log("constructor is working");
  }
// ______________________
  async componentDidMount() {
    const response = await axios.get("http://localhost:8080/home");
    const moviesData = response.data.movies;
    console.log(moviesData);
    console.log("componentDidMount is working");
    this.setState({ moviesTop: moviesData });

    const res = await axios.get("http://localhost:8080/home/tv");
    const tvshowData = res.data.tvshow;
    console.log(tvshowData);
    console.log("componentDidMount2 is working");
    this.setState({ tvShowsPopular: tvshowData });
  }

// _______________________
// search movie
  handleSubmit = e => {
    e.preventDefault();
    axios.get(`https://api.themoviedb.org/3/search/movie?api_key=${myKey}&query=${this.state.searchTerm}`)
    .then(res => {
      console.log(res.data.results);
      this.setState({
      movies: [...res.data.results],
      totalResuolts: res.data.total_results
    })
      
    }).catch(err => {

      console.log(err);
    });
    console.log('search is done');
  };
 

  handleChange = e => {
    this.setState({ searchTerm: e.target.value });
  };
// _______________________
  movieInfo = movie => {
    this.setState({
      flag: true,
      movie: movie
    });
  };
// _______________________

addItemToList = movie =>{
  console.log(movie.id,movie.title);
  const item = movie.id;
  if(item == movie.id){
    const response = axios.get("http://localhost:8080/cart/id");
    console.log(response)
    const movieId = response.data.movie_id;
    console.log(this.movieId);
    console.log("yes it work");
    console.log(this.userList);
    this.setState({ userList: movieId });

  }
 };


  render() {

    if (this.state.flag) {
      return (
        <Redirect
          to={{
            pathname: "/ShowPage",
            movie: this.state.movie
          }}
        />
      );
    }
// _______________________

    const homeMovies = this.state.moviesTop.map((movie, index) => (
      <Col md="3">
        <Card className="movie-card">
          <Card.Img
            onClick={() => this.movieInfo(movie)}
            variant="top "
            src={`${baseImgUrl}/${movie.poster_path}`}
            alt={"a"}
          />

          <Card.Body>
            <Card.Title className="card-title" key={index}>
              {movie.title}
            </Card.Title>
            <Card.Text className="card-text">{movie.overview}</Card.Text>
            <Card.Text className="card-text">{movie.vote_average}</Card.Text>
            <button onClick={() => this.addItemToList(movie)}>Add</button>
          </Card.Body>
        </Card>
      </Col>
    ));
// ______________________
    const homeTvshow = this.state.tvShowsPopular.map((movie, index) => (
      <Col md="3">
        <Card className="movie-card">
          <Card.Img
            onClick={() => this.movieInfo(movie)}
            variant="top "
            src={`${baseImgUrl}/${movie.poster_path}`}
            alt={"a"}
          />
          <Card.Body>
            <Card.Title className="card-title" key={index}>
              {movie.name}
            </Card.Title>
            <Card.Text className="card-text">{movie.overview}</Card.Text>
            <Card.Text className="card-text">{movie.vote_average}</Card.Text>
          </Card.Body>
        </Card>
      </Col>
    ));
// ______________________
    return (
      <div className="Home ">
        <h1>Welcome To Foxx Movie</h1>
        <Container>
          <SearchArea
            handleSubmit={this.handleSubmit}
            handleChange={this.handleChange}
          />
        </Container>

        <Container>
          <Row>
            <MovieList movies={this.state.movies} />
          </Row>
        </Container>

        <div className="allMovies">
          <Container>
            <Row>
              {!this.state.totalResuolts ? homeMovies : ""}
              {!this.state.totalResuolts ? homeTvshow : ""}
            </Row>
          </Container>
        </div>
      </div>
    );
  }
}

export default Home;
